* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer {
  margin-top: 20px;
  display: flex;
  font-family: 'Indie Flower', cursive;
  font-size: 30px;
  padding-left: 25px;
}

.field {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
}

.value {
  text-align: center;
  font-size: 50px;
}

#datetime-picker {
  min-height: 40px;
  width: 300px;
  border-radius: 4px;
  border-color: #252424;
  margin-left: 20px;
}

#start-button {
  margin-left: 10px;
  width: 100px;
  min-height: 40px;
  background-color: #fff;
  font-family: 'Indie Flower', cursive;
  font-size: 20px;
  font-weight: bold;
}
